/* eslint-disable @typescript-eslint/no-explicit-any */
import { Action, ActionPanel, List } from "@raycast/api";
import { getConfig, handleConfigError } from "./config";
import { extractKeyboardShortcuts } from "./shortcuts";

export default function listTemplate({ programName }: { programName: string }) {
  const { config, error } = getConfig(programName);
  if (error) {
    handleConfigError(error);
  }
  if (config) {
    const keyboardShortcuts = extractKeyboardShortcuts(config as any);

    console.debug(`${programName} shortcuts:`, keyboardShortcuts);

    return (
      <List navigationTitle="Keyboard Shortcuts" searchBarPlaceholder="Search your shortcuts">
        {Object.entries(keyboardShortcuts).map(([key, value]) => {
          return (
            <List.Item
              key={key}
              icon={"list-icon.png"}
              title={value.description}
              subtitle={value.shortcut}
              accessories={[{ text: value.mode }]}
              actions={
                <ActionPanel>
                  <Action.CopyToClipboard title="Activate" content={value.shortcut} />
                </ActionPanel>
              }
            />
          );
        })}
      </List>
    );
  }
}
